// src/components/LoginForm.js
import React, { useState } from "react";
import { Row, Col, Form, Input, Button, Typography, message } from "antd";
import { UserOutlined, LockOutlined } from "@ant-design/icons";
import { login } from "../utils";

const { Title, Text } = Typography;

export default function LoginForm({ onSuccess, onSignUp }) {
  const [loading, setLoading] = useState(false);
  const [form] = Form.useForm();

  // 随机拉一张餐厅／美食图，绝对不会 404
  const bgUrl = "https://images.unsplash.com/photo-1517248135467-4c7edcad34c4?auto=format&fit=crop&w=1950&q=80";

  const handleFinish = ({ username, password }) => {
    setLoading(true);
    login({ username, password })
      .then(() => {
        message.success("Sign in successful");
        onSuccess();
      })
      .catch((err) => {
        message.error(err.message);
      })
      .finally(() => setLoading(false));
  };

  return (
    <Row
      justify="center"
      align="middle"
      style={{
        position: "relative",
        height: "100vh",
        padding: "0 16px",
        overflow: "hidden",
      }}
    >
      {/* 模糊背景 */}
      <div
        style={{
          position: "absolute",
          inset: 0,
          backgroundImage: `url(${bgUrl})`,
          backgroundSize: "cover",
          backgroundPosition: "center",
          filter: "blur(8px)",
          zIndex: 0,
        }}
      />

      <Col xs={24} sm={20} md={16} lg={12} xl={8} style={{ zIndex: 1 }}>
        <div
          style={{
            background: "rgba(255,255,255,0.9)",
            padding: 40,
            borderRadius: 8,
            boxShadow: "0 4px 12px rgba(0,0,0,0.15)",
          }}
        >
          {/* 标题 */}
          <div style={{ textAlign: "center", marginBottom: 32 }}>
            <Title
              style={{
                fontFamily: '"Pacifico", cursive',
                fontSize: 48,
                fontWeight: 700,
                background: "linear-gradient(90deg, #FF6B6B 0%, #FFD93D 100%)",
                WebkitBackgroundClip: "text",
                WebkitTextFillColor: "transparent",
                textShadow: "2px 2px 4px rgba(0,0,0,0.2)",
              }}
            >
              OnlineOrder
            </Title>
            <Text type="secondary" style={{ fontSize: 16 }}>
              Sign in to your account
            </Text>
          </div>

          {/* 登录表单 */}
          <Form form={form} name="login" onFinish={handleFinish} layout="vertical">
            <Form.Item
              name="username"
              rules={[
                { required: true, message: "Please enter your email or phone" },
              ]}
            >
              <Input
                size="large"
                prefix={<UserOutlined />}
                placeholder="Email or Phone Number"
              />
            </Form.Item>

            <Form.Item
              name="password"
              rules={[{ required: true, message: "Please enter your password" }]}
            >
              <Input.Password
                size="large"
                prefix={<LockOutlined />}
                placeholder="Password"
              />
            </Form.Item>

            <Form.Item>
              <Button
                htmlType="submit"
                loading={loading}
                block
                shape="round"
                style={{
                  background: "#1890ff",
                  borderColor: "#1890ff",
                  height: 48,
                  fontSize: 16,
                  fontWeight: 600,
                  color: "#fff",
                }}
              >
                Sign In
              </Button>
            </Form.Item>

            {/* 同样样式的 Sign Up */}
            <Form.Item>
              <Button
                onClick={onSignUp}
                block
                shape="round"
                style={{
                  background: "#1890ff",
                  borderColor: "#1890ff",
                  height: 48,
                  fontSize: 16,
                  fontWeight: 600,
                  color: "#fff",
                }}
              >
                Sign Up
              </Button>
            </Form.Item>
          </Form>
        </div>
      </Col>
    </Row>
  );
}